import './AdminPedidos.css'

import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import Select from 'react-select'

import { AdminItemField, Page } from '../../components'
import { useStores } from '../../models/root-store/root-store-context'
import moment from "moment-timezone";
import {useToaster} from "../../helpers-ts";

const FileDownload = require('js-file-download')

interface FiltroVentaProductos {
  fecha_inicio: string
  fecha_fin: string
  categoria: number
  fabricante: number
  cliente: number
}

interface FiltroHojaRuta {
  fecha_inicio: string
  fecha_fin: string
  transportista: number
  tipo_vehiculo: number
}

interface FiltroComprasFabricantesPendientes {
  fecha_inicio: string
  fecha_fin: string
  fabricante: number
}

interface FiltroEntregasTransportistaPendientes {
  fecha_inicio: string
  fecha_fin: string
  transportista: number
}

interface FiltroFacturasPendientes {
  fecha_inicio: string
  fecha_fin: string
  tipo_persona: number
  persona_id: number
}

export const AdminReportes: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [Tipo, setTipo] = useState<any>({ value: 10, label: 'Venta de productos' })
  const [TipoId, setTipoId] = useState<number>(10)
  const [Fabricantes, setFabricantes] = useState<any[]>([])
  const [Transportistas, setTransportistas] = useState<any[]>([])
  const [Categorias, setCategorias] = useState<any[]>([])
  const [Clientes, setClientes] = useState<any[]>([])
  const [TiposVehiculo, setTiposVehiculo] = useState<any[]>([])
  const [TipoPersona, setTipoPersona] = useState<any>({ value: 10, label: 'Fabricante' })
  const [Data, setData] = useState<any[]>([])
  const [Ready, setReady] = useState(false)
  const [PersonaRendir, setPersonaRendir] = useState<any>()

  const [DatosFiltro, setDatosFiltro] = useState({} as any)
  const [DatosFiltroVentaProductos, setDatosFiltroVentaProductos] = useState<FiltroVentaProductos | null>()
  const [DatosFiltroHojaRuta, setDatosFiltroHojaRuta] = useState<FiltroHojaRuta | null>()
  const [DatosFiltroComprasFabricantesPendientes, setDatosFiltroComprasFabricantesPendientes] =
    useState<FiltroComprasFabricantesPendientes | null>()
  const [DatosFiltroEntregasTransportistaPendientes, setDatosFiltroEntregasTransportistaPendientes] =
    useState<FiltroEntregasTransportistaPendientes | null>()
  const [DatosFiltroFacturasPendientes, setDatosFiltroFacturasPendientes] = useState<FiltroFacturasPendientes | null>()
  const addToast = useToaster()

  const tiposReporte = [
    { value: 10, label: 'Venta de productos' },
    { value: 20, label: 'Hojas de ruta' },
    { value: 30, label: 'Compras de proveedores pendientes de facturación' },
    { value: 40, label: 'Entregas de drivers pendientes de facturación' },
    { value: 50, label: 'Facturas pendientes de pago' },
    { value: 60, label: 'Calificaciones' },
  ]

  const setup = () => {
    obtenerFabricantes()
    obtenerTransportistas()
    obtenerCategorias()
    obtenerClientes()
    obtenerTiposVehiculos()
    setReady(true)
    setDatoFiltroFacturasPendientes('tipo_persona', 10)
  }

  React.useEffect(() => {
    setup()
  }, [])

  const obtenerFabricantes = () => {
    rootStore.environment.api.fabricantesList().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data.map((dat: any) => {
          return { value: dat.id, label: dat.nombre }
        })
        setFabricantes(data)
      }
    })
  }

  const obtenerTransportistas = () => {
    rootStore.environment.api.transportistaList().then((result) => {
      if (result.kind === 'ok') {
        setTransportistas(
          result.data.map((dat: any) => {
            return { value: dat.id, label: dat.nombre }
          }),
        )
      }
    })
  }

  const obtenerCategorias = () => {
    rootStore.environment.api.categorias().then((result) => {
      if (result.kind === 'ok') {
        setCategorias(
          result.categorias.map((dat: any) => {
            return { value: dat.id, label: dat.nombre }
          }),
        )
      }
    })
  }

  const obtenerClientes = () => {
    rootStore.environment.api.clientes().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data as any
        setClientes(
          data.results.map((dat: any) => {
            return { value: dat.id, label: dat.email }
          }),
        )
      }
    })
  }

  const obtenerTiposVehiculos = () => {
    rootStore.environment.api.tiposVehiculos().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data as any
        setTiposVehiculo(
          data.results.map((dat: any) => {
            return { value: dat.id, label: dat.nombre }
          }),
        )
      }
    })
  }

  const obtenerReporte = () => {
    let filtros
    switch (TipoId) {
      case 10:
        filtros = DatosFiltroVentaProductos
        break
      case 20:
        filtros = DatosFiltroHojaRuta
        break
      case 30:
        filtros = DatosFiltroComprasFabricantesPendientes
        break
      case 40:
        filtros = DatosFiltroEntregasTransportistaPendientes
        break
      case 50:
        filtros = DatosFiltroFacturasPendientes
        break
      case 60:
        filtros = DatosFiltro
        break
    }

    if (filtros) {
      if (  filtros.fecha_hasta
          || filtros.fecha_fin
          || filtros.fecha_inicio
          || filtros.fecha_desde
      ) {
        let fecha_hasta = filtros.fecha_fin ? filtros.fecha_fin : filtros.fecha_hasta;
        let fecha_desde = filtros.fecha_inicio ? filtros.fecha_inicio : filtros.fecha_desde;
        if (!moment(fecha_hasta).isValid() || !moment(fecha_desde).isValid()) {
          addToast('Formato de fecha no válido.', 'error');
          return;
        }
      }
    }

    rootStore.environment.api.getReportes(TipoId, filtros).then((result) => {
      const reporteSeleccionado = tiposReporte.find((rep) => rep.value === TipoId)
      if (!reporteSeleccionado) {
        return
      }
      if (result.kind === 'ok') {
        FileDownload(result.response, 'Reporte - ' + reporteSeleccionado.label + '.xls')
      }
    })
  }

  const setDatoFiltroVentaProductos = (campo: string, valor: string) => {
    let datos = { ...DatosFiltroVentaProductos }
    // @ts-ignore
    datos[campo] = valor
    setDatosFiltroVentaProductos(datos as FiltroVentaProductos)
  }

  const setDatoHojaRuta = (campo: string, valor: string) => {
    let datos = { ...DatosFiltroHojaRuta }
    // @ts-ignore
    datos[campo] = valor
    setDatosFiltroHojaRuta(datos as FiltroHojaRuta)
  }

  const setDatoComprasFabricantesPendientes = (campo: string, valor: string) => {
    let datos = { ...DatosFiltroComprasFabricantesPendientes }
    // @ts-ignore
    datos[campo] = valor
    setDatosFiltroComprasFabricantesPendientes(datos as FiltroComprasFabricantesPendientes)
  }

  const setDatoEntregasTransportistaPendientes = (campo: string, valor: string) => {
    let datos = { ...DatosFiltroEntregasTransportistaPendientes }
    // @ts-ignore
    datos[campo] = valor
    setDatosFiltroEntregasTransportistaPendientes(datos as FiltroEntregasTransportistaPendientes)
  }

  const setDatoFiltroFacturasPendientes = (campo: string, valor: any) => {
    let datos = { ...DatosFiltroFacturasPendientes }
    // @ts-ignore
    datos[campo] = valor
    setDatosFiltroFacturasPendientes(datos as FiltroFacturasPendientes)
  }

  const setDatoFiltro = (campo: string, valor: any) => {
    let datos = { ...DatosFiltro }
    datos[campo] = valor
    setDatosFiltro(datos)
  }

  useEffect(() => {
    if (Ready) {
      if (TipoPersona && TipoPersona.value === 10) {
        setData(Fabricantes)
      } else {
        setData(Transportistas)
      }
      setPersonaRendir(null)
    }
  }, [TipoPersona])

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h2 className="text-themecolor">Reportes</h2>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Reportes</li>
              </ol>
            </div>
          </div>
          <div className="card">
            <div className="card-body">
              <div className={'row'}>
                <div className={'col-6'}>
                  <h4 className={'mb-4'}>Tipo de reporte</h4>
                  <Select
                    defaultValue={Tipo}
                    onChange={(val) => {
                      setTipo(val)
                      val && setTipoId(val.value)
                    }}
                    options={tiposReporte}
                    theme={(theme) => ({
                      ...theme,
                      colors: {
                        ...theme.colors,
                        primary: '#303AB2',
                        neutral50: 'black',
                      },
                    })}
                  />
                </div>
              </div>
            </div>
          </div>
          {Tipo && Tipo.value === 10 && (
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-12'}>
                    <h4 className={'mb-4'}>Venta de productos</h4>
                  </div>
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Fecha inicio'}
                    tipo={'fecha'}
                    campo={'fecha_inicio'}
                    valor={
                      DatosFiltroVentaProductos && DatosFiltroVentaProductos.fecha_inicio
                        ? DatosFiltroVentaProductos.fecha_inicio
                        : null
                    }
                    set_dato={setDatoFiltroVentaProductos}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Fecha fin'}
                    tipo={'fecha'}
                    campo={'fecha_fin'}
                    valor={
                      DatosFiltroVentaProductos && DatosFiltroVentaProductos.fecha_fin
                        ? DatosFiltroVentaProductos.fecha_fin
                        : null
                    }
                    set_dato={setDatoFiltroVentaProductos}
                    columnas={3}
                  />
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Categoría'}
                    opciones_select={Categorias}
                    tipo={'select2'}
                    campo={'categoria'}
                    valor={DatosFiltroVentaProductos && DatosFiltroVentaProductos.categoria}
                    set_dato={setDatoFiltroVentaProductos}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Proveedor'}
                    opciones_select={Fabricantes}
                    tipo={'select2'}
                    campo={'fabricante'}
                    valor={DatosFiltroVentaProductos && DatosFiltroVentaProductos.fabricante}
                    set_dato={setDatoFiltroVentaProductos}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Cliente'}
                    opciones_select={Clientes}
                    tipo={'select2'}
                    campo={'cliente'}
                    valor={DatosFiltroVentaProductos && DatosFiltroVentaProductos.cliente}
                    set_dato={setDatoFiltroVentaProductos}
                    columnas={3}
                  />
                </div>
              </div>
            </div>
          )}
          {Tipo && Tipo.value === 20 && (
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-12'}>
                    <h4 className={'mb-4'}>Hojas de ruta</h4>
                  </div>
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Fecha inicio'}
                    tipo={'fecha'}
                    campo={'fecha_inicio'}
                    valor={
                      DatosFiltroHojaRuta && DatosFiltroHojaRuta.fecha_inicio ? DatosFiltroHojaRuta.fecha_inicio : null
                    }
                    set_dato={setDatoHojaRuta}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Fecha fin'}
                    tipo={'fecha'}
                    campo={'fecha_fin'}
                    valor={DatosFiltroHojaRuta && DatosFiltroHojaRuta.fecha_fin ? DatosFiltroHojaRuta.fecha_fin : null}
                    set_dato={setDatoHojaRuta}
                    columnas={3}
                  />
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Driver'}
                    opciones_select={Transportistas}
                    tipo={'select2'}
                    campo={'transportista'}
                    valor={DatosFiltroHojaRuta && DatosFiltroHojaRuta.transportista}
                    set_dato={setDatoHojaRuta}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Tipo de vehículo'}
                    opciones_select={TiposVehiculo}
                    tipo={'select2'}
                    campo={'tipo_vehiculo'}
                    valor={DatosFiltroHojaRuta && DatosFiltroHojaRuta.tipo_vehiculo}
                    set_dato={setDatoHojaRuta}
                    columnas={3}
                  />
                </div>
              </div>
            </div>
          )}
          {Tipo && Tipo.value === 30 && (
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-12'}>
                    <h4 className={'mb-4'}>Compras de fabricantes pendientes de facturación</h4>
                  </div>
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Fecha de compra inicio'}
                    tipo={'fecha'}
                    campo={'fecha_inicio'}
                    valor={
                      DatosFiltroComprasFabricantesPendientes && DatosFiltroComprasFabricantesPendientes.fecha_inicio
                        ? DatosFiltroComprasFabricantesPendientes.fecha_inicio
                        : null
                    }
                    set_dato={setDatoComprasFabricantesPendientes}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Fecha de compra fin'}
                    tipo={'fecha'}
                    campo={'fecha_fin'}
                    valor={
                      DatosFiltroComprasFabricantesPendientes && DatosFiltroComprasFabricantesPendientes.fecha_fin
                        ? DatosFiltroComprasFabricantesPendientes.fecha_fin
                        : null
                    }
                    set_dato={setDatoComprasFabricantesPendientes}
                    columnas={3}
                  />
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Proveedor'}
                    opciones_select={Fabricantes}
                    tipo={'select2'}
                    campo={'fabricante'}
                    valor={
                      DatosFiltroComprasFabricantesPendientes && DatosFiltroComprasFabricantesPendientes.fabricante
                    }
                    set_dato={setDatoComprasFabricantesPendientes}
                    columnas={3}
                  />
                </div>
              </div>
            </div>
          )}
          {Tipo && Tipo.value === 40 && (
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-12'}>
                    <h4 className={'mb-4'}>Entregas de drivers pendientes de facturación</h4>
                  </div>
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Fecha de hoja de ruta inicio'}
                    tipo={'fecha'}
                    campo={'fecha_inicio'}
                    valor={
                      DatosFiltroEntregasTransportistaPendientes &&
                      DatosFiltroEntregasTransportistaPendientes.fecha_inicio
                        ? DatosFiltroEntregasTransportistaPendientes.fecha_inicio
                        : null
                    }
                    set_dato={setDatoEntregasTransportistaPendientes}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Fecha de hoja de ruta fin'}
                    tipo={'fecha'}
                    campo={'fecha_fin'}
                    valor={
                      DatosFiltroEntregasTransportistaPendientes && DatosFiltroEntregasTransportistaPendientes.fecha_fin
                        ? DatosFiltroEntregasTransportistaPendientes.fecha_fin
                        : null
                    }
                    set_dato={setDatoEntregasTransportistaPendientes}
                    columnas={3}
                  />
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Driver'}
                    opciones_select={Transportistas}
                    tipo={'select2'}
                    campo={'transportista'}
                    valor={
                      DatosFiltroEntregasTransportistaPendientes &&
                      DatosFiltroEntregasTransportistaPendientes.transportista
                    }
                    set_dato={setDatoEntregasTransportistaPendientes}
                    columnas={3}
                  />
                </div>
              </div>
            </div>
          )}
          {Tipo && Tipo.value === 50 && (
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-12'}>
                    <h4 className={'mb-4'}>Facturas pendientes de pago</h4>
                  </div>
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Fecha de vencimiento factura inicio'}
                    tipo={'fecha'}
                    campo={'fecha_inicio'}
                    valor={
                      DatosFiltroFacturasPendientes && DatosFiltroFacturasPendientes.fecha_inicio
                        ? DatosFiltroFacturasPendientes.fecha_inicio
                        : null
                    }
                    set_dato={setDatoFiltroFacturasPendientes}
                    columnas={3}
                  />
                  <AdminItemField
                    titulo={'Fecha de vencimiento factura  fin'}
                    tipo={'fecha'}
                    campo={'fecha_fin'}
                    valor={
                      DatosFiltroFacturasPendientes && DatosFiltroFacturasPendientes.fecha_fin
                        ? DatosFiltroFacturasPendientes.fecha_fin
                        : null
                    }
                    set_dato={setDatoFiltroFacturasPendientes}
                    columnas={3}
                  />
                </div>
                <div className={'row'}>
                  <div className={'col-3'}>
                    <h4 className={'mb-4'}>Tipo</h4>
                    <Select
                      defaultValue={TipoPersona}
                      onChange={(val) => {
                        setTipoPersona(val)
                        val && setDatoFiltroFacturasPendientes('tipo_persona', val.value)
                      }}
                      options={[
                        { value: 10, label: 'Fabricante' },
                        { value: 20, label: 'Transportista' },
                      ]}
                      theme={(theme) => ({
                        ...theme,
                        colors: {
                          ...theme.colors,
                          primary: '#303AB2',
                          neutral50: 'black',
                        },
                      })}
                    />
                  </div>
                  <div className={'col-3'}>
                    <h4 className={'mb-4'}>Persona</h4>
                    <Select
                      value={PersonaRendir}
                      onChange={(val) => {
                        setPersonaRendir(val)
                        val && setDatoFiltroFacturasPendientes('persona_id', val.value)
                      }}
                      options={Data}
                      isSearchable
                      placeholder={'Seleccionar persona'}
                      isClearable
                      theme={(theme) => ({
                        ...theme,
                        colors: {
                          ...theme.colors,
                          primary: '#303AB2',
                          neutral50: 'black',
                        },
                      })}
                    />
                  </div>
                </div>
              </div>
            </div>
          )}
          {Tipo && Tipo.value === 60 && (
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-12 col-lg-6 col-xl-3 form-group'}>
                    <label className={'font-weight-bold text-gray'}>
                      Tipo
                    </label>
                    <Select
                      defaultValue={'T' as any}
                      onChange={obj => setDatoFiltro('tipo', obj.value)}
                      options={[
                        { value: 'T', label: 'Transportista' },
                        { value: 'F', label: 'Fabricante' },
                      ]}
                      theme={(theme) => ({
                        ...theme,
                        colors: {
                          ...theme.colors,
                          primary: '#303AB2',
                          neutral50: 'black',
                        },
                      })}
                    />
                  </div>
                  <AdminItemField
                    titulo={'Fecha desde'}
                    tipo={'fecha'}
                    campo={'fecha_desde'}
                    valor={DatosFiltro.fecha_inicio}
                    set_dato={setDatoFiltro}
                    columnas={4}
                  />
                  <AdminItemField
                    titulo={'Fecha hasta'}
                    tipo={'fecha'}
                    campo={'fecha_hasta'}
                    valor={DatosFiltro.fecha_hasta}
                    set_dato={setDatoFiltro}
                    columnas={4}
                  />
                </div>
              </div>
            </div>
          )}
          <div className={'row'}>
            <div className={'col-12'}>
              <div className={'text-right mt-3'}>
                <div className={'btn-group ml-auto'}>
                  <div className={'text-right'}>
                    <button
                      className={'btn btn-primary'}
                      // disabled={!HabilitarGenerarFactura}
                      title={'Descargar reporte'}
                      onClick={() => obtenerReporte()}
                    >
                      <FIcon.FileText size={14} style={{ marginBottom: 3 }} /> Descargar reporte
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminReportes
